<template>
  <div class="app-container">
    <el-row>
      <el-col :xs="12" :sm="12" :lg="24" class="card-panel-col">
        <el-table
          :data="all"
          border
          fits
          stripe
          highlight-current-row
          class="table table-striped scrollbar-demo-item"
        >
          <el-table-column label="Raif" width="57px">
            <template slot-scope="scope">
              {{ scope.$index + 1 }}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            height="100"
            label="AssetIdentification_layer"
          >
            <template slot-scope="row">
              <el-input
                v-model="yes"
                size="mini"
                placeholder="write"
                class="text-start"
              />

              <span>{{ row.personsFirstName }} </span>
            </template>
          </el-table-column>
          <el-table-column
            label="AssetIdentification_layer"
            prop="personsNationalIdPk"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-select
                ref="headerSearchSelect"
                v-model="list.Layer"
                filterable
                clearable
                remote
                placeholder="Select"
                class="filter-item"
                id="dropnational"
              >
                <el-option
                  v-for="item in layer"
                  :key="item.val"
                  :value="item.val"
                  :label="item.lbl"
                />
              </el-select>
            </template>
          </el-table-column>

          <el-table-column
            align="center"
            height="100"
            label="Attack Entry Point"
          >
            <template slot-scope="row">
              <el-input
                v-model="list.AttackEntryPoint"
                size="mini"
                placeholder="write"
                class="text-start"
              />

              <span>{{ row.personsFirstName }} </span>
            </template>
          </el-table-column>
          <el-table-column
            label="Impact S"
            prop="personsNationalIdPk"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-select
                ref="headerSearchSelect"
                v-model="list.Impact_S"
                filterable
                clearable
                remote
                placeholder="Select"
                class="filter-item"
                id="dropnational"
                @change="change"
              >
                <el-option
                  v-for="item in ImpactS"
                  :key="item.val"
                  :value="item.val"
                  :label="item.lbl"
                />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column
            label="Impact O"
            prop="personsNationalIdPk"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-select
                ref="headerSearchSelect"
                v-model="list.Impact_O"
                filterable
                clearable
                remote
                placeholder="Select"
                class="filter-item"
                id="dropnationalo"
                @change="change"
              >
                <el-option
                  v-for="item in ImpactO"
                  :key="item.val"
                  :value="item.val"
                  :label="item.lbl"
                />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column
            label="Targeted Accuracy"
            prop="personsNationalIdPk"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-select
                ref="headerSearchSelect"
                v-model="list.TargetedAccuracy"
                filterable
                clearable
                remote
                placeholder="Select"
                class="filter-item"
                id="dropnational"
                @change="change"
              >
                <el-option
                  v-for="item in TargetedAccuracy"
                  :key="item.val"
                  :value="item.val"
                  :label="item.lbl"
                />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column align="center" height="100" label="Result">
            <template slot-scope="row">
              <el-input
                v-model="list.Impact_rating_Result"
                size="mini"
                class="text-start"
              />
            </template>
          </el-table-column>
          <el-table-column
            label="Knowledge"
            prop="personsNationalIdPk"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-select
                ref="headerSearchSelect"
                v-model="list.Knowledge"
                filterable
                clearable
                remote
                placeholder="Select"
                class="filter-item"
                id="dropnational"
                @change="change_attack_feasibility"

              >
                <el-option
                  v-for="item in Knowledge"
                  :key="item.val"
                  :value="item.val"
                  :label="item.lbl"
                />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column
            label="Equipment"
            prop="personsNationalIdPk"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-select
                ref="headerSearchSelect"
                v-model="list.Equipment"
                filterable
                clearable
                remote
                placeholder="Select"
                class="filter-item"
                id="dropnational"
                @change="change_attack_feasibility"

              >
                <el-option
                  v-for="item in Equipment"
                  :key="item.val"
                  :value="item.val"
                  :label="item.lbl"
                />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column
            label="Window of Opportunity"
            prop="personsNationalIdPk"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-select
                ref="headerSearchSelect"
                v-model="list.Window_of_Opportunity"
                filterable
                clearable
                remote
                placeholder="Select"
                class="filter-item"
                id="dropnational"
                @change="change_attack_feasibility"
              >
                <el-option
                  v-for="item in WindowofOpportunity"
                  :key="item.val"
                  :value="item.val"
                  :label="item.lbl"
                />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column
            label="Specialist Expertise"
            prop="personsNationalIdPk"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-select
                ref="headerSearchSelect"
                v-model="list.SpecialistExpertise"
                filterable
                clearable
                remote
                placeholder="Select"
                class="filter-item"
                id="dropnational"
                @change="change_attack_feasibility"
              >
                <el-option
                  v-for="item in SpecialistExpertise"
                  :key="item.val"
                  :value="item.val"
                  :label="item.lbl"
                />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column
            label="Result"
            align="center"
          >
            <template slot-scope="{ row }">
              <el-input
                v-model="list.Attack_feasibility_Result"
                size="mini"
                class="text-start"
              />
            </template>
          </el-table-column>
         
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "grid",
  data() {
    return {
      work: [],
      layer: [
        {
          lbl: "1 Physical World",
          val: "1",
        },
        {
          lbl: "2 Sensor Layer",
          val: "2",
        },
        {
          lbl: "3 Data Preparation Layer",
          val: "3",
        },
        {
          lbl: "4 Application Layer",
          val: "4",
        },
      ],
      ImpactS: [
        {
          lbl: "Severe",
          val: "3",
        },
        {
          lbl: "Major",
          val: "2",
        },
        {
          lbl: "Moderate",
          val: "1",
        },
        {
          lbl: "Negligible",
          val: "0",
        },
      ],
      ImpactO: [
        {
          lbl: "Severe",
          val: "3",
        },
        {
          lbl: "Major",
          val: "2",
        },
        {
          lbl: "Moderate",
          val: "1",
        },
        {
          lbl: "Negligible",
          val: "0",
        },
      ],
      TargetedAccuracy: [
        {
          lbl: "Targeted",
          val: "3",
        },
        {
          lbl: "Untargeted",
          val: "1",
        },
      ],
      Knowledge: [
        {
          lbl: "White-Box",
          val: "11",
        },
        {
          lbl: "Gray-Box",
          val: "5",
        },
        {
          lbl: "Black-Box",
          val: "0",
        },
      ],
      Equipment: [
        {
          lbl: "Standard",
          val: "0",
        },
        {
          lbl: "Specialized",
          val: "4",
        },
        {
          lbl: "Bespoke",
          val: "7",
        },
        {
          lbl: "Multiple Bespoke",
          val: "9",
        },
      ],
      WindowofOpportunity: [
        {
          lbl: "remote",
          val: "w1_10",
        },
        {
          lbl: "< 0.1m",
          val: "w2_10",
        },
        {
          lbl: "< 0.5m",
          val: "w1_4",
        },
      
        {
          lbl: "<1m",
          val: "w2_4",
        },
        {
          lbl: "< 10m",
          val: "w_1",
        },
        {
          lbl: "< 100m",
          val: "w_0",
        },
      ],
      SpecialistExpertise: [
        {
          lbl: "Layman",
          val: "0",
        },
        {
          lbl: "Proficient",
          val: "3",
        },
        {
          lbl: "Expert",
          val: "6",
        },
        {
          lbl: "Multiple Experts",
          val: "8",
        },
      ],
      all: [
        {
          personsFirstName: "azac",
        },
      ],
      yes: "",
      list: {
        Work: "",
        Layer: "",
        AttackEntryPoint: "",
        Impact_S: "",
        Impact_O: "",
        TargetedAccuracy: "",
        Impact_rating_Result: "",
        Knowledge: "",
        Equipment: "",
        Window_of_Opportunity: "",
        SpecialistExpertise: "",
        Attack_feasibility_Result: "",
        Overall_Risk_determination: "",
      },
      result: "",
      result_attack_feasibility: "",
    };
  },
  methods: {
    change() {
      if (
        this.list.Impact_S.length > 0 &&
        this.list.Impact_O.length > 0 &&
        this.list.TargetedAccuracy.length > 0
      ) {
        this.result =
          parseInt(this.list.Impact_S) +
          parseInt(this.list.Impact_O) +
          parseInt(this.list.TargetedAccuracy);
        console.log(typeof this.result, this.result);
        if (6 < this.result < 10) {
          this.list.Impact_rating_Result = "Severe";
        }
        if (this.result === 1 || this.result === 2) {
          this.list.Impact_rating_Result = "Negligible";
        }
        // if (1 <= this.result <= 2) {
        //   this.list.Impact_rating_Result = "Negligible";
        // }
        if (this.result === 3 || this.result === 4) {
          this.list.Impact_rating_Result = "Moderate";
        }
        if (this.result === 5 || this.result === 6) {
          this.list.Impact_rating_Result = "Major";
        }
      }
    },
    change_attack_feasibility() {
      if (
        this.list.Knowledge.length > 0 &&
        this.list.Equipment.length > 0 &&
        this.list.Window_of_Opportunity.length > 0 &&
        this.list.SpecialistExpertise.length > 0
      ) {
        const W_O=this.list.Window_of_Opportunity.split("_");
        this.result_attack_feasibility =
          parseInt(this.list.Knowledge) +
          parseInt(this.list.Equipment) +
          parseInt(W_O[1]) +
          parseInt(this.list.SpecialistExpertise);
console.log(this.result_attack_feasibility,"xvd")
        if (
          this.result_attack_feasibility >= 0 &&
          this.result_attack_feasibility <= 13
        ) {
          this.list.Attack_feasibility_Result = "High";
        }
        if (
          this.result_attack_feasibility >= 14 &&
          this.result_attack_feasibility <= 19
        ) {
          this.list.Attack_feasibility_Result = "Medium";
        }
        if (
          this.result_attack_feasibility >= 20 &&
          this.result_attack_feasibility <= 24
        ) {
          this.list.Attack_feasibility_Result = "Low";
        }
        if (
          this.result_attack_feasibility >= 25 &&
          this.result_attack_feasibility <= 38
        ) {
          this.list.Attack_feasibility_Result = "Very Low";
        }
      }
    },
  },
};
</script>

